.email:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.email {
  transition: all 1s linear;
}

.my-name {
  font-family: "Kaushan Script";
  margin: 1rem 0;
  font-size: 1.7rem;
}

.social-icons:hover {
  color: yellow;
}

.navbar-item {
  margin-right: 1rem;
  font-size: 1.3rem;
  color: black;
  cursor: pointer;
}

.navbar-active {
  margin-left: 1rem;
  font-size: 2rem;
  font-weight: 650;
  border-bottom: 4px solid #f89999;
}

.navbar-item:hover {
  background: -webkit-linear-gradient(#8d2020, #ffc8c8);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 600px) {
  .navbar-active {
    font-size: 1.5rem;
  }
  .navbar-item {
    font-size: 1rem;
    margin-right: 0.5rem;
  }
}

.projectCard-wrapper {
  margin: 2rem 0px;
  width: 15rem;
  height: 15rem;
  /* min-height: 6rem; */
}

.projectCard-image {
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  border: 0.25rem solid #f89999;
  transition: -webkit-transform 0.3s linear;
  transition: transform 0.3s linear;
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}

.projectCard-image:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.projectCard-title {
  margin-top: 0.5rem;
  font: size 1rem;
  display: flex;
  /* align-items: center; */
  justify-content: space-evenly;
  margin-bottom: 1rem;
}

.portfolio {
  height: 72vh;
  padding-top: 2rem;
  overflow-y: auto;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(to right bottom, #ff5858, #f6c0ba);
}
/* ff5858 */
/* f89999 */
.project-navbar {
  font-size: 1rem;
  cursor: pointer;
  font-weight: bold;
}

.proj-navbar-item {
  padding: 0.5rem;
}

@media (max-width: 600px) {
  .proj-navbar-item {
    padding: 0.2rem;
  }
}

.row-height {
  height: 90vh;
}

.sidebar-custom-styles {
  background-color: #ffffff;
  height: 100%;
  box-shadow: 0 0 10px black;
}

.content-custom-styles {
  background-color: #ffffff;
  height: 100%;
  box-shadow: 0 0 10px black;
}

@media (max-width: 991px) {
  .sidebar {
    margin-bottom: 1rem;
  }
}

@media (max-width: 1022px) {
  .row-height {
    height: unset;
  }
  .main-component {
    margin-bottom: 1rem;
  }
}

.content {
  /* background: linear-gradient(
    90deg,
    hsla(0, 100%, 67%, 1) 0%,
    hsla(0, 100%, 89%, 1) 100%
  ); */

  background: linear-gradient(
